import { api } from "@/utils/api.js";
import { mapGetters, mapActions, mapMutations } from "vuex";
import nullData from "@/components/nullData.vue";

export default {
    components: {
        nullData
    },
    props: {
        goodsid: {
            type: String,
            default: ''
        }
    },
    mounted(){
        this.alleva();
    },
    methods: {
        async alleva(){
            let data = {
                plat: '3',
                page: 1,
                goods_id: this.goodsid,
                type: this.nowIndex
            }
            const [err, res] = await api.newevaluatelist(data);
            if(err){
                this.$vux.toast.text(err.msg);
                return;
            }
            
            if(res.code == 2000){
                if (res.data.list == "") {
                    this.nullShow = true;
                    this.evalist = "";
                } else {
                    this.evalist = res.data.list;
                    this.evaluate_num = res.data.evaluate_num;
                    this.nullShow = false;
                }
                this.$emit('conFn',{})
            }
        },
        changeNav(index){
            this.$vux.loading.show();
            this.nowIndex = index;
            this.upLoadingComplete = false;
            this.upLoading = true;
            this.alleva();
        }
    },
    data(){
        return {
            tabBar: [
                {name: '全部'},
                {name: '好评'},
                {name: '中评'},
                {name: '差评'},
                {name: '有图'},
                {name: '追加评价'},
            ],
            nowIndex: 0,
            page: 2,
            evalist: "",
            nullShow: false,
            upLoading: false,
            upLoadingComplete: false,
            evaluate_num:[],
        }
    },
    computed: {
        ...mapGetters(["user", "account", "token"])
    },
}



